import React from 'react';
import styled from 'styled-components';

const Item = styled.div`
    padding: 10px 5px;
    font-family: sans-serif;
`;

export const ChatItem = ({ uuid, messageWrapper }) => {
    return (
        <Item>
            {messageWrapper.uuid === uuid ? 'ME: ':''}{messageWrapper.message}
        </Item>
    )
};
